<template>
  <div class="home">
    <!-- <img alt="Vue logo" src="../assets/logo.png"> -->
    <v-container fluid grid-list-md>
    <v-layout justify-center row>
      <v-flex xs6>
        <v-radio-group v-model="gender" row>
          <v-radio label="男性" value="1"></v-radio>
          <v-radio label="女性" value="2"></v-radio>
        </v-radio-group>
        <v-textarea
          solo
          name="input-7-4"
          label="ぐち"
          auto-grow
          placeholder="自由に書いてください"
          v-model="guchi_text"
        ></v-textarea>
        <v-btn block dark @click="guchi" class="mb-5">ぐちる</v-btn>
        <Card v-for="(data, i) in display_data" :key="i" :data="data"/>
        <div class="text-xs-center mb-2 mt-2" v-if="loading">
          <v-progress-circular
            indeterminate
            color="primary"
          ></v-progress-circular>
        </div>
        <div class="more-area text-xs-center mt-4" v-else>
          <div v-if="exist_more">
            <div class="more-text grey--text lighten-1">もっと見る</div>
            <v-icon x-large @click="more">details</v-icon>
          </div>
        </div>
      </v-flex>
    </v-layout>
    </v-container>
  </div>
</template>

<script>
// @ is an alias to /src
import Card from '@/components/Card'
import firebase from 'firebase'
import 'firebase/firestore'

export default {
  name: 'home',
  components: {
    Card
  },
  data: function () {
    return {
      gender: '1',
      display_data: [],
      guchi_text: '',
      db: null,
      loading: true,
      exist_more: true
    }
  },
  methods: {
    guchi: function () {
      let _this = this
      let input_data = {
        gender: parseInt(this.gender),
        guchi: this.guchi_text,
        timestamp: new Date().getTime()
      }
      this.db.collection('guchies').add(input_data)
        .then(function (docRef) {
          _this.display_data.splice(0, 0, input_data)
          _this.init_input()
        })
    },
    init_input: function () {
      this.guchi_text = ''
    },
    more: function () {
      let add_guchi_count = 10
      this.loading = true
      let _this = this
      let last_timestamp = this.display_data[this.display_data.length - 1].timestamp
      this.db.collection('guchies').orderBy('timestamp', 'desc').startAfter(last_timestamp).limit(add_guchi_count)
        .get().then(function (query) {
          query.forEach(function (doc) { _this.display_data.push(doc.data()) })
          _this.loading = false
          if (query.size < add_guchi_count) {
            _this.exist_more = false
          }
        })
    }
  },
  mounted: function () {
    let init_guchi_count = 3
    if (firebase.apps.length === 0) {
      firebase.initializeApp({
        apiKey: 'AIzaSyCVWvS0kcXlhioH7eUH-nQAe7OYMJ4DZyg',
        authDomain: 'helical-beaker-217402.firebaseapp.com',
        projectId: 'helical-beaker-217402',
        databaseURL: 'https://helical-beaker-217402.firebaseio.com',
        storageBucket: 'helical-beaker-217402.appspot.com',
        messagingSenderId: '220025841162'
      })
    }
    this.db = firebase.firestore()
    this.db.settings({ timestampsInSnapshots: true })
    let _this = this
    this.db.collection('guchies').orderBy('timestamp', 'desc').limit(init_guchi_count).get().then(function (query) {
      query.forEach(function (doc) { _this.display_data.push(doc.data()) })
      _this.loading = false
    })
  }
}
</script>
<style scoped>
.more-text {
  font-size: .8rem;
}
</style>
