<template>
  <v-card :color="display_color" class="white--text mb-2">
    <div class="pa-2">
      <span class="font-size-1-2">{{ data.guchi }}</span>
      <p class="text-xs-right mb-0 font-size-0-8">{{ display_date }}</p>
    </div>
  </v-card>
</template>
<script>
export default {
  name: 'Card',
  props: {
    data: Object
  },
  computed: {
    display_date: function () {
      let d = new Date(this.data.timestamp)
      return `${d.getMonth() + 1}/${d.getDate()} ${d.getHours()}:${d.getMinutes()}`
    },
    display_color: function () {
      return this.data.gender == '1' ? 'indigo darken-1' : 'pink darken-1'
    }
  }
}
</script>
<style scoped>
.font-size-1-2 {
  font-size: 1.2rem;
}
.font-size-0-8 {
  font-size: .8rem;
}
</style>
